<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-spin :spinning="spinning">
            <a-card>
                <div class="xwsh_nr">
                    <div class="pad-30">
                        <h1>{{paper.name}}</h1>
                        <h2>
                            分数：{{result.score}} 分&nbsp;&nbsp;&nbsp;
                            是否通过：{{result.is_passed?'是':'否'}}&nbsp;&nbsp;&nbsp;
                            开始考试时间：{{result.created_at_format}}&nbsp;&nbsp;&nbsp;
                            交卷时间：{{result.updated_at_format}}&nbsp;&nbsp;&nbsp;
                        </h2>

                        <Questions v-model="questions"></Questions>

                    </div>
                </div>
            </a-card>
        </a-spin>
    </div>
</template>

<script>
    import TopButtons from '../在线考试列表/components/TopButtons'
    import CheckArticle from '@/components/article/check/Index'
    import {getExamResult} from '@/api/examResult'
    import Questions from '@/components/exam/questions'

    export default {
        components: {
            TopButtons, CheckArticle,Questions
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        data() {
            return {
                spinning: true,
                result_id: 0,
                result: {},
                paper: {},
                questions: [],
                countDownTimestamp: 0,
                modalData: {},
                visible: false,
            }
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            },
        },
        methods: {
            init() {
                this.spinning = true;
                getExamResult(this.thisId)
                    .then(res => {
                        this.result = res.data;
                        this.paper = res.data.paper;
                        this.questions = res.data.answers_snapshot;
                        this.spinning = false;
                    })
            },
        },
        beforeRouteLeave(to, from, next) {
            next();
        },
        created() {
            this.init();
        }
    }
</script>
